/********other mixins **********/
.html-login-page .center-1 {
  margin: 0px;
}
.html-login-page .master-wrapper-page .master-wrapper-content {
  width: auto;
}
.html-login-page .master-wrapper-page .master-wrapper-content .topic-block {
  position: relative;
}
.html-login-page .master-wrapper-page .master-wrapper-content .topic-block .topic-block-title {
  display: none;
}
.html-login-page .master-wrapper-page .master-wrapper-content .topic-block .topic-block-body p img {
  width: 100%;
  height: 456px;
}
.html-login-page .login-page .login-header .header-image {
  text-align: center;
  min-height: 64px;
  border-bottom: 1px solid #DEE1E3;
}
.html-login-page .login-page .signInBlock {
  background: #ffffff;
}
.html-login-page .login-page .infoBlock {
  background-color: #f0862e;
}
.html-login-page .login-page .returning-wrapper {
  float: none;
  width: auto;
}
.html-login-page .login-page .returning-wrapper .form-fields {
  background-color: #ffffff;
  padding: 5px;
  width: 294px;
  margin: 0px auto;
}
@media only screen and (max-width: 575px) {
  .html-login-page .login-page .returning-wrapper .form-fields {
    width: 100%;
  }
}
@media only screen and (min-width: 992px) {
  .html-login-page .login-page .returning-wrapper .form-fields {
    width: 370px;
  }
}
.html-login-page .login-page .returning-wrapper .form-fields #signInHead {
  width: 100%;
  text-align: left;
  color: #24282f;
  font-weight: 600;
  font-size: 1.375rem;
  margin-bottom: 20px;
}
.html-login-page .login-page .returning-wrapper .form-fields .inputs {
  text-align: center;
  margin-bottom: 25px;
}
@media only screen and (max-width: 575px) {
  .html-login-page .login-page .returning-wrapper .form-fields .inputs {
    margin-bottom: 10px;
  }
}
.html-login-page .login-page .returning-wrapper .form-fields .buttons {
  margin: 0px;
  padding: 0;
}
.html-login-page .login-page .returning-wrapper .form-fields .buttons .login-button {
  height: 48px;
  letter-spacing: 0.05em;
  text-transform: capitalize;
  font-weight: 700;
  color: #ffffff;
  width: 100%;
  margin-left: 3px;
  font-size: 1.25rem;
  border-radius: 4px;
  border-radius: 50px;
}
@media only screen and (max-width: 575px) {
  .html-login-page .login-page .returning-wrapper .form-fields .buttons .login-button {
    font-size: 0.875rem;
  }
}
.html-login-page .login-page .returning-wrapper .form-fields .reversed {
  margin-top: 10px;
}
.html-login-page .login-page .returning-wrapper .form-fields .reversed .left-side-wrap {
  float: left;
}
.html-login-page .login-page .returning-wrapper .form-fields .reversed .left-side-wrap input {
  position: absolute;
  left: -999px;
}
.html-login-page .login-page .returning-wrapper .form-fields .reversed .left-side-wrap label {
  display: flex;
  align-items: center;
  font-size: 0.75rem;
}
@media only screen and (min-width: 992px) {
  .html-login-page .login-page .returning-wrapper .form-fields .reversed .left-side-wrap label {
    font-size: 0.875rem;
  }
}
.html-login-page .login-page .returning-wrapper .form-fields .reversed .left-side-wrap label:before {
  content: "";
  margin-left: 0px;
}
.html-login-page .login-page .returning-wrapper .form-fields .reversed .right-side-wrap {
  float: right;
}
.html-login-page .login-page .returning-wrapper .form-fields .reversed .right-side-wrap .forgot-password a {
  font-size: 0.75rem;
  margin: 0px;
}
@media only screen and (min-width: 992px) {
  .html-login-page .login-page .returning-wrapper .form-fields .reversed .right-side-wrap .forgot-password a {
    font-size: 0.875rem;
  }
}
.html-login-page .login-page .returning-wrapper .form-fields .reversed .right-side-wrap .forgot-password a:hover {
  color: #A02121;
}
.html-login-page .login-page .returning-wrapper input[type=email], .html-login-page .login-page .returning-wrapper input[type=password] {
  border: 2px solid #DEE1E3;
  width: 100%;
  height: 48px;
  background-color: #f7f7f7;
  color: #0f121b;
  border-radius: 50px;
  -webkit-box-shadow: none;
  border: 1px solid transparent;
  padding-left: 20px;
  padding-right: 20px;
}
@media only screen and (max-width: 575px) {
  .html-login-page .login-page .returning-wrapper input[type=email], .html-login-page .login-page .returning-wrapper input[type=password] {
    font-size: 0.75rem;
  }
}
.html-login-page .login-page .returning-wrapper .show-text {
  position: relative;
}
.html-login-page .login-page .returning-wrapper .show-text .text {
  right: 5px;
  color: #f0862e;
  position: absolute;
}
.html-login-page .login-page .customer-blocks {
  margin: 50px 0px;
}
@media only screen and (max-width: 575px) {
  .html-login-page .login-page .customer-blocks {
    margin-top: 100px;
  }
}
.html-login-page .login-page .customer-blocks .block1 {
  padding-left: 0px;
  padding-right: 0px;
}
.html-login-page .login-page .customer-blocks .login-image {
  height: 100%;
}
.html-login-page .login-page .wrap {
  webkit-box-shadow: 0px 10px 34px -15px rgba(0, 0, 0, 0.24);
  -moz-box-shadow: 0px 10px 34px -15px rgba(0, 0, 0, 0.24);
  box-shadow: 0px 10px 34px -15px rgb(0, 0, 0);
  height: 420px;
}
@media only screen and (max-width: 575px) {
  .html-login-page .login-page .wrap {
    height: auto;
  }
}
@media only screen and (min-width: 768px) {
  .html-login-page .login-page .wrap {
    height: 375px;
  }
}
@media only screen and (min-width: 992px) {
  .html-login-page .login-page .wrap {
    height: 415px;
  }
}
